<template>
    <img :src="imgSrc" alt="jesse" @click="click" id="jesse">
    <p>Clicked: {{ clickCount }}</p>
    <p>Click power: {{ clickPower }}</p>
    <p>Autoclicker amount: {{ autoClickerAmount }}</p>

    <button @click="upgrade">Upgrade ({{ upgradeCost }})</button>
    <button @click="buyAutoClicker">AutoClicker ({{ autoClickerCost }})</button>
    <br><br>
    <label>Audio: </label><input type="checkbox" v-model="audio">
</template>

<script>
export default {
    name: "JesseClicker",
    data() {
        return {
            clickCount: 0,
            upgradeCost: 10,
            imgSrc: require("@/assets/jesse.jpg"),
            audioSrc: require("@/assets/vau_v2.mp3"),
            clickPower: 1,
            autoClickerAmount: 1,
            autoClickerCost: 10,
            currentRot: 0,
            audio: true,
        }
    },
    methods: {
        click() {
            this.clickCount += this.clickPower
            if(this.audio) new Audio(this.audioSrc).play()
            this.currentRot += 360;
            document.querySelector("#jesse").style.transform = "rotate(" + this.currentRot + "deg)";
        },
        upgrade() {
            if (this.clickCount >= this.upgradeCost) {
                this.clickCount -= this.upgradeCost
                this.upgradeCost *= 2
                this.clickPower++
            }

        },
        buyAutoClicker() {
            if (this.clickCount >= this.autoClickerCost) {
                this.clickCount -= this.autoClickerCost
                this.autoClickerCost *= 2
                this.autoClickerAmount++
            }
        },
        save() {
            localStorage.setItem('jesse_score', this.clickCount);
            localStorage.setItem('jesse_power', this.clickPower);
            localStorage.setItem('jesse_auto', this.autoClickerAmount);
            localStorage.setItem('jesse_auto_cost', this.autoClickerCost);
            localStorage.setItem('jesse_power_cost', this.upgradeCost);
            localStorage.setItem('last_played', new Date().getTime())
        }
    },
    watch: {
        autoClickerAmount(val) {
            //Clears all intervals
            const interval_id = window.setInterval(function () {
            }, Number.MAX_SAFE_INTEGER);
            for (let i = 1; i < interval_id; i++) {
                window.clearInterval(i);
            }

            console.log(val)
            if (this.autoClickerAmount > 0) {
                for (let i = 0; i < this.autoClickerAmount; i++) {
                    window.setInterval(() => {
                        this.clickCount++
                    }, 1000)
                }
            }
            window.setInterval(() => {
                this.save()
            }, 5000)
        }
    },
    mounted() {
        let score = localStorage.getItem("jesse_score");
        this.clickCount = score != null ? parseInt(score) : 0
        this.clickPower = localStorage.getItem("jesse_power") != null ? parseInt(localStorage.getItem("jesse_power")) : 1
        this.autoClickerAmount = localStorage.getItem("jesse_auto") != null ? parseInt(localStorage.getItem("jesse_auto")) : 0
        this.autoClickerCost = localStorage.getItem("jesse_auto_cost") != null ? parseInt(localStorage.getItem("jesse_auto_cost")) : 10
        this.upgradeCost = localStorage.getItem("jesse_power_cost") != null ? parseInt(localStorage.getItem("jesse_power_cost")) : 10

        let lastTimeString = localStorage.getItem("last_played")
        if(lastTimeString != null) {
            let lastDate = new Date(parseInt(lastTimeString)).getTime()
            console.log(lastTimeString)
            console.log(lastDate)
            let differenceMillis = new Date().getTime() - lastDate
            console.log(differenceMillis)
            let differenceSec = differenceMillis / 1000
            console.log(differenceSec)
            this.clickCount += Math.floor(differenceSec) * this.autoClickerAmount;
        }

        if (this.autoClickerAmount > 0) {
            for (let i = 0; i < this.autoClickerAmount; i++) {
                window.setInterval(() => {
                    this.clickCount++
                }, 1000)
            }
        }
        window.setInterval(() => {
            this.save()
        }, 5000)
    }
}
</script>

<style scoped>
p {
    color: white;
}
label {
    color: white;
}

#jesse {
    transition: transform 0.5s ease-in;
}
</style>